/**
 * Service file to handle API requests related to bookings.
 * This service abstracts the HTTP requests for booking-related operations,
 * ensuring a clean and reusable API layer.
 */

const BASE_URL = process.env.NEXT_PUBLIC_API_URL as string;
// ✅ Uses environment variable for flexibility across dev, staging, and production

// -------------------------
// Booking Type Definition
// -------------------------
export interface Booking {
  coordinatorId: string;
  name: string;
  email: string;
  weddingDate: string;
  guestNumber: number;
}

// -------------------------
// Create a New Booking
// -------------------------

/**
 * Sends a POST request to create a new booking for a wedding coordinator.
 *
 * @param data - The booking details provided by the client.
 * @returns Promise resolving with the booking response from the backend API.
 * @throws Error if the request fails or the server responds with an error status.
 */
export const createBooking = async (data: Booking): Promise<unknown> => {
  const response = await fetch(`${BASE_URL}/api/bookings`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(data),
  });

  if (!response.ok) {
    const error = await response.json();
    throw new Error(error.message || "Failed to create booking");
  }

  return response.json();
};
